.navbar {
    background-color: black;
    color: white;
    height: 60px;
    padding: 0 55px;
}

.navbar-container {
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.navbar-brand {
    color: white;
    font-weight: 700;
    font-size: 18px;
    text-decoration: none;
}

.navbar-result {
    position: fixed;
    top: 5px;
    left: 0;
    text-align: center;
    width: 100%;
}

.navbar-message {
    background-color: #FFDD40;
    padding: 5px;
    border-radius: 3px;
    color: black;
    opacity: 0;
    -webkit-transition: opacity 2s;
    -o-transition: opacity 2s;
    transition: opacity 2s;
}

.navbar-message_show {
    opacity: 1;
}

.navbar-right {
    display: -ms-flexbox;
    display: flex;
}

.navbar-button {
    background-color: #4d4d50;
    border-radius: 3px;
    width: 108px;
    height: 40px;
}

.navbar-save {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 10px;
    background-color: #47CF73;
    cursor: pointer;
}

.navbar-delete {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 10px;
    background-color: #E70041;
    cursor: pointer;
}

.navbar-hide {
    display: none;
}

.navbar-create {
    color: white;
    text-decoration: none;
    display: block;
    line-height: 40px;
    text-align: center;
}

.navbar-login_logout {
    -ms-flex-item-align: center;
        align-self: center;
}

.navbar-right-logged-in {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 20px;
}

.navbar-picture {
    display: block;
    height: 40px;
}

.navbar-picture img {
    width: 40px;
}


